.page {
  font-family: Inter, Arial, sans-serif;
  font-size: 16px;
  font-display: optional;
  scroll-behavior: smooth;
  overflow-x: hidden;

  &:has(.sidebar:target) {
    overflow: hidden;
  }

  &__content {
    max-width: 1440px;
    margin: 0 auto;
    background-color: $color-bg;
    overflow-x: hidden;
  }
}
